const catchCountry = document.querySelector('.type-country'); 
const resultsContainer = document.createElement('ul');
document.body.appendChild(resultsContainer);
let allCountries = [];
fetch("https://restcountries.com/v2/all?fields=name,flag,capital,population,languages")
  .then(response => response.json())
  .then(data => {
    allCountries = data;
  })
  .catch(error => {
    console.error("Помилка при завантаженні країн:", error);
  });
catchCountry.addEventListener('input', () => {
  const searchText = catchCountry.value.toLowerCase().trim();
  resultsContainer.innerHTML = '';
  if (searchText === '') return;
  const filtered = allCountries.filter(country =>
    country.name.toLowerCase().includes(searchText)
  );
  if (filtered.length === 0) {
    resultsContainer.innerHTML = '<li>Країн не знайдено</li>';
    return;
  }
  if (filtered.length > 10) {
    PNotify.notice({
      text: 'Занадто багато збігів. Уточніть запит.',
      delay: 500
    });
    return;
  }
  if (filtered.length === 1) {
    const country = filtered[0];
    const languages = country.languages.map(lang => lang.name).join('');
    resultsContainer.innerHTML = `
      <li class="countryCard">
        <h2>${country.name}</h2>
        <img src="${country.flag}">
        <p>Столиця: ${country.capital}</p>
        <p>Населення: ${country.population.toLocaleString()}</p>
        <p>Мови:${languages}</p>
      </li>
    `;
  } else {
    resultsContainer.innerHTML = filtered.map(country =>
      `<li>${country.name}</li>`
    ).join('');
  }
});